* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100vh;
  background: #f4f4f4;
  margin: 0;
}

body {
  position:relative;
  padding: 0;
  margin: 0;
  font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont,
    "Helvetica Neue", "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕",
    나눔고딕, "Nanum Gothic", arial, 돋움, Dotum, Tahoma, Geneva, sans-serif;
}
